<template>
	<view class="header">
		<slot name="bg"></slot>
		<!-- 头部导航 -->
		<view :style="{width: '100%',height: statusBarClientRectTop}"></view>
		<view class="headerTitle disp-flex disp-justifyBetween align-items">
			
			<view  @click="goBack()" >
				<image class="headerBack" :src="imgUrl+ 'back.png'"></image>
			</view>
			<view class="headerTitle-name flex nowrapName">
				<slot name="titleName"></slot>
			</view>
		</view>
		<!-- 头部导航 end -->
		<!-- 头部菜单 -->
		<view class="headerMenu disp-flex disp-justifyBetween">
			<view v-for="item in menuList" :class="menuIndex == item.type ? 'headerMenu-list-active headerMenu-list' : 'headerMenu-list'"
				@click="topMenu(item.type)">
				{{item.name}}
			</view>
		</view>
		<!-- 头部菜单 end -->
		<view class="menu-data-box">
			<view class="menu-data disp-flex">
				<view class="data-list font1" v-if="menuIndex == 'day' && calendarData.indexOf(',') == -1" v-for="(item, index) in day" :key="index"
					@click="onSelectData(index)">
					<view :class="item == timeValue ? 'data-list-active' : ''" class="data-list-txt font">
					{{item.split('-')[1]}}/{{item.split('-')[2]}}
					</view>
				</view>
				<view class="data-list font1" v-if="menuIndex == 'day' && calendarData.indexOf(',') != -1" >
					<view class="data-list-active data-list-txt font">
					{{ calendarData.split(',')[0]}} ~ {{calendarData.split(',')[1] }}
					</view>
				</view>
				<view class="data-list font1" v-if="menuIndex == 'week'" v-for="(item, index) in week" :key="index"
					@click="onSelectData(index)">
					<view :class="item.split('-W')[0] == timeValue.split('-')[0] &&item.split('-W')[1] == timeValue.split('-')[1]  ? 'data-list-active' : ''" class="data-list-txt font">
					{{item}}
					</view>
				</view>
				<view class="data-list font1" v-if="menuIndex == 'month'" v-for="(item, index) in month" :key="index"
					@click="onSelectData(index)">
					<view :class="item.split('-')[0] == timeValue.split('-')[0] &&Number(item.split('-')[1]) == Number(timeValue.split('-')[1]) ? 'data-list-active' : ''" class="data-list-txt font">
					{{item}}
					</view>
				</view>
				<view class="data-list font1" v-if="menuIndex == 'season'" v-for="(item, index) in season" :key="index"
					@click="onSelectData(index)">
					<view :class="item.split('-Q')[0] == timeValue.split('-')[0] &&item.split('-Q')[1] == timeValue.split('-')[1] ? 'data-list-active' : ''" class="data-list-txt font">
					{{item}}
					</view>
				</view>
				<view class="data-list font1" v-if="menuIndex == 'year'" v-for="(item, index) in year" :key="index"
					@click="onSelectData(index)">
					<view :class="item == timeValue ? 'data-list-active' : ''" class="data-list-txt font">
					{{item}}
					</view>
				</view>
			</view>
			<view class="select-data disp-flex disp-justifyEnd">
				<!-- <view class="select-linear-gradient"></view> -->
<!-- 				<view v-if="menuIndex != 'year' && menuIndex != 'day'&& !showDayData" class="font title-data">
				  {{current.finance_year}}-{{menuIndex == 'season' && 'Q'+selectData || menuIndex == 'week' && 'W'+selectData || menuIndex == 'month' && selectData}}
				</view>
				<view v-if="menuIndex == 'day' && !showDayData" class="font title-data">{{selectData}}</view>
				<view v-if="menuIndex == 'year' && !showDayData" class="font title-data">{{current.finance_year}}</view> -->
				<view class="select-right" @click="showCalendar()">
					<view class="select-txt disp-flex align-items">
						<image class="select-icon" :src="imgUrl+'rili.jpg'"></image>
						{{ selectDataName }}
					</view>
				</view>
			</view>
		</view>
		
		<!-- 日历选择器弹框 -->
		<uni-drawer ref="showCalendar" mode="right">
			<scroll-view class="scroll-view-box" scroll-y="true">
				<view class="all-channels all-calendar">
					<view class="close" @click="closeCalendar('showCalendar')">
						<image :src="imgUrl+'cdetails_icon_goodsmore_wps (1).png'"></image>
					</view>
					<view class="calendar-title">选择日期</view>
					
					<view>
						<!-- 日历 -->
						<uni-calendar ref="childRef" :end-date="'2025-02-07'" class="uni-calendar--hook" :date="timeValue" :selected="calenderData" :range="false" :showMonth="false" @change="calendarChange" />
						<!-- 日历 end -->
						<!-- 确定按钮 -->
						<view class="calendar-btn disp-flex">
							<view class="calendar-btn-list" @click="calendarCancel()">取消
							</view>
							<view class="calendar-btn-list" @click="calendarDefine()">确认
							</view>
						</view>
						<!-- 确定按钮end -->
					</view>
				</view>
			</scroll-view>
		</uni-drawer>
		<!-- 日历选择器弹框 end --> 
	   
	   <!-- 底部选择器 -->
	   <view class="mask-box" @click.stop="closePopup" v-if="maskFlag">
	   	<view class="bottom-popup">
	   		<view class="popup-btn disp-flex align-items disp-justifyBetween">
	   			<view class="popup-btn-item" @click.stop="closePopup">取消</view>
	   			<view class="popup-btn-item determine" @click="popupChange">
	   				确定</view>
	   		</view>
	   	
	   		<picker-view v-if="pickerShow" class="picker-content" indicator-class="uni-picker-view-indicator"
	   			:value="selectDataValue" @change="bindPickerChange">
	   			<picker-view-column>
	   				<view class="picker-item" v-for="(item, index) in years" :key="index">{{ item }}年</view>
	   			</picker-view-column>
	   			<picker-view-column v-if="menuIndex == 'week'">
	   				<view class="picker-item" v-for="(item, index) in weeks" :key="index">{{ item }}周</view>
	   			</picker-view-column>
	   			<picker-view-column v-if="menuIndex == 'month'">
	   				<view class="picker-item" v-for="(item, index) in months" :key="index">{{ item }}月</view>
	   			</picker-view-column>
				<picker-view-column v-if="menuIndex == 'season'">
					<view class="picker-item" v-for="(item, index) in seasons" :key="index">{{ item }}季</view>
				</picker-view-column>
			</picker-view>
	   	</view>
	   </view>
	   <!-- 底部选择器end -->
	</view>
</template>

<script>
	import {
		getWeekArray,
		getYearsArray,
		getMonthArray,
		getSeasonArray,
		getYear,
		getMonth,
		formatTime,
		getWeekNum,
		getQuarter
	} from '../../utils/utils.js'
	import { getHeaderTimes } from '../../utils/http.js'
	const APP = getApp();
export default {
	name: 'headerNavigation',
	data() {
		return {
			imgUrl: APP.globalData.imgUrl,
			menuIndex: 'day',
			selectDataName: "天",
			menuList: [
				{name: '天', type: 'day'},
				{name: '周', type: 'week'},
				{name: '月', type: 'month'},
				{name: '季', type: 'season'},
				{name: '年', type: 'year'},
			],
			selectData: '',
			maskFlag: false,
			pickerShow: false,
			showDayData: true,
			selectDataValue: [],
			timeValue:'',
			calendarData: '',
			calendarCheckData: [],
			calenderData:[],
			cStartDate: '',
			cEndDate: '',
			years: getYearsArray(getYear().toYear),
			weeks: getWeekArray(getWeekNum().toWeek),
			months: getMonthArray(getMonth().toMonth),
			seasons: getSeasonArray(getQuarter().toSeason),
			current: {
				toYear: getYear(),
				toMonth: getMonth(), 
				toWeek: getWeekNum(), 
				toSeason: getQuarter()
			},
			dayIndex: 0,
			day: [],
			week: [],
			month: [],
			season: [],
			year: []
		}
	},
	computed:{
		statusBarClientRectTop(){
			var that = this;
			// #ifdef H5
			return '10px'
			// #endif
			
			// #ifdef APP-PLUS
			return (uni.getSystemInfoSync().statusBarHeight + 2) *2 + 'rpx'
			// #endif
					
			// #ifdef MP-WEIXIN
			  return `${
				uni.getMenuButtonBoundingClientRect().top +2
			  }px`;
			  // #endif 
						    
		},
	},
	mounted() {
	
		const today = new Date();
		const yesterday = new Date(today);
		yesterday.setDate(today.getDate());
		this.selectData = formatTime(yesterday)
		var year= getYear()
		var month= getMonth()
		var week= getWeekNum()
		var season= getQuarter()
		
		this.getTimeDateList('day', formatTime(yesterday), year)
		this.getTimeDateList('week', week, year)
		this.getTimeDateList('month', month, year)
		this.getTimeDateList('season', season, year)
		this.getTimeDateList('year', year, year)
		this.getEmitData('day', formatTime(yesterday))
		this.calenderData.push({
			date: formatTime(new Date().setDate(today.getDate())),
			info: '今日'
		})
	},
	methods: {
		goBack() {
			uni.navigateBack();
		},
		getTimeDateList(type, time, year){
			var data = {}
			if(type == 'day'){
				var month = time.split('-')[1]
				var day = time.split('-')[2]
				data={
					'data-type': type,
					year: year,
					month: month,
					day: day
				}
			} else if(type == 'year'){
				data={
					'data-type': type,
					year: year
				}
			} else if(type == 'month'){
				data={
					'data-type': type,
					year: year,
					month: time
				}
			} else if(type == 'week' || type == 'season'){
				data={
					'data-type': type,
					year: year,
					period: time
				}
			}
			 
			getHeaderTimes(data).then(res => {
				this[type] = res.data.reverse()
			})
		},
		onSelectData(index) {
			this.dayIndex=index
			this.getTabDate()
		},
		getTabDate(){
			let that = this;
			let timeValue = ''
			if (that.menuIndex == 'day') {
				timeValue = this.day[this.dayIndex]
			} else if (that.menuIndex == 'week') { //周
				timeValue = this.week[this.dayIndex].split('W')[0]+this.week[this.dayIndex].split('W')[1]
			} else if (that.menuIndex == 'season') { //季
				timeValue = this.season[this.dayIndex].split('Q')[0]+this.season[this.dayIndex].split('Q')[1]
			} else if (that.menuIndex == 'month') { //月
				timeValue = this.month[this.dayIndex]
			} else if (that.menuIndex == 'year') { //年
				timeValue = this.year[this.dayIndex]
			}
			
			that.getEmitData(that.menuIndex, timeValue)
		},
		// 菜单切换
		topMenu(dataType) {
			this.menuIndex = dataType;
		    this.showDayData = true
			this.dayIndex = 0
			switch (dataType) {
				case "day":
					this.selectDataName = '天';
				    this.selectData = this.day[0]
					this.getEmitData(dataType, this.selectData)
					break;
				case "season":
					this.selectDataName = '季';
					this.selectData = this.current.toSeason
					this.selectDataValue = [this.years.length-1, this.current.toSeason-1]
					this.getEmitData(dataType, this.current.toYear + '-' + this.current.toSeason)
					break;
				case "week":
					this.selectDataName = '周';
					this.selectData = this.current.toWeek
					this.selectDataValue = [this.years.length-1, this.current.toWeek-1]
					this.getEmitData(dataType, this.current.toYear + '-' + this.current.toWeek)
					break;
				case "month":
					this.selectDataName = '月';
					this.selectData = this.current.toMonth
					this.selectDataValue = [this.years.length-1, this.current.toMonth-1]
					this.getEmitData(dataType, this.current.toYear + '-' + this.current.toMonth)
					break;
				case "year":
					this.selectDataName = '年';
					this.selectData = this.current.toYear
					this.selectDataValue = [this.years.length-1]
					this.getEmitData(dataType, this.current.toYear)
					break;
			}
			this.getTimeDateList(dataType, this.selectData, this.current.toYear)
			
			
			// this.getNewData()
			
		},
		// 获取菜单日期
		getEmitData(dataType, dataValue) {
			const time = {
				timeType: dataType,
				timeValue: dataValue,
			}
			this.timeValue = dataValue
			this.$emit('menuChange', time)
		},
		// 显示日期选择器
		showCalendar() {
			if (this.menuIndex === 'day') {
				this.$refs.showCalendar.open(); //日历选择器
			} else {
				this.maskFlag = true;
			    this.pickerShow = true;
			}
		},
		// 关闭日历选择器
		closeCalendar() {
			this.$refs.showCalendar.close();
		},
		// 关闭底部弹框
		closePopup() {
			this.maskFlag = false;
			this.pickerShow = false;
			
		},
		// 确认
		popupChange(){
			this.maskFlag = false;
			this.showDayData = false
			
			this.getNewData()
		},
		getNewData(){
			let that = this;
			let timeValue = ''
			if (that.menuIndex == 'day') {
				timeValue = this.day[this.dayIndex]
			} else if (that.menuIndex == 'week') { //周
				timeValue = `${this.years[this.selectDataValue[0]]}-${this.weeks[this.selectDataValue[1]]}`
			} else if (that.menuIndex == 'season') { //季
				timeValue = `${this.years[this.selectDataValue[0]]}-${this.seasons[this.selectDataValue[1]]}`
			} else if (that.menuIndex == 'month') { //月
				timeValue = `${this.years[this.selectDataValue[0]]}-${this.months[this.selectDataValue[1]]}`
			} else if (that.menuIndex == 'year') { //年
				timeValue = `${this.years[this.selectDataValue[0]]}`
			}
			
			if(that.menuIndex == 'year'){
				that.getTimeDateList(that.menuIndex, this[`${that.menuIndex}s`][this.selectDataValue[0]], this.years[this.selectDataValue[0]])
			} else {
				that.getTimeDateList(that.menuIndex, this[`${that.menuIndex}s`][this.selectDataValue[1]], this.years[this.selectDataValue[0]])
			}
			
			that.getEmitData(that.menuIndex, timeValue)
		},
		bindPickerChange(e){
			if(uni.getStorageSync('current').toYear != this.years[e.detail.value[0]]){
				this.years= getYearsArray(uni.getStorageSync('current').toYear)
				this.weeks= getWeekArray()
				this.months= getMonthArray()
				this.seasons= getSeasonArray()
			}else{
				this.years= getYearsArray(uni.getStorageSync('current').toYear)
				this.weeks= getWeekArray(uni.getStorageSync('current').toWeek)
				this.months= getMonthArray(uni.getStorageSync('current').toMonth)
				this.seasons= getSeasonArray(uni.getStorageSync('current').toSeason)
			}
			
			this.selectDataValue = e.detail.value;
		},
		calendarChange(e) {
		    let that = this;
			if (e.range.after !== '' && e.range.before !== '') {
				this.calenderData = [];
				this.calenderData.push({
					date: e.range.before > e.range.after ? e.range.after : e.range.before,
					info: '开始'
				})
				this.calenderData.push({
					date: e.range.before > e.range.after ? e.range.before : e.range.after,
					info: '结束'
				})
			} else {
				this.calenderData = [];
			}
			if ((e.range.after > e.range.before) || e.range.after == '') {
				that.cStartDate = e.range.before;
				that.cEndDate = e.range.after;
			} else {
				that.cStartDate = e.range.after;
				that.cEndDate = e.range.before;
			}
			if(e.range.data.length == 0){
				this.calendarData = e.fulldate
			} else {
				if ((e.range.after > e.range.before) || e.range.after == '') {
					this.calendarData = `${e.range.before},${e.range.after}`;
					
				} else {
					this.calendarData = `${e.range.after},${e.range.before}`;
				}
			}
			this.calendarData = e.fulldate
			
		},
		selectRange(index) {
		
			this.$refs.childRef.consolea({
				after: this.calendarData.split(',')[0],
				before: this.calendarData.split(',')[1]
			});
		},
		calendarCancel() {
			this.$refs.showCalendar.close();
		},
		// 确定选择的日历
		calendarDefine() {
			this.showDayData = false
			this.selectData = this.calendarData;
			this.dayIndex = 0
			this.getTimeDateList(this.menuIndex, this.calendarData, this.calendarData.split('-')[0])
			this.getEmitData(this.menuIndex, this.calendarData)
			this.$refs.showCalendar.close();
		}
		
	}
}
</script>

